import { Component, OnInit } from '@angular/core';
import { Http, Headers, RequestOptions, Response } from '@angular/http';
import { Router } from '@angular/router';
import {LeaveService} from '../leaves/leaves.service';
import { Cookie } from 'ng2-cookies/ng2-cookies';

import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';

@Component({
 selector: 'app-cancelleave',
  templateUrl: './cancelleave.component.html',
  styleUrls: ['./cancelleave.component.css'],
  providers:[LeaveService]
})
export class CancelleaveComponent implements OnInit{

    public value: boolean = false;
    public valuec: boolean = false;
  Datas:any=[];
  LeaveDatas:any;
  d_Data:any=[];
  deleteDatas:any;
  public checkdata:any;
  public success:any=false;
 private url_deleteLeave = "http://localhost:3000/empId/leaves/leaveId";

  constructor(private router:Router, private leaveService: LeaveService, private http: Http) { }
  

  public userid=Cookie.get('cookieName');

  ngOnInit(){
    this.leaveService.getLeave().subscribe(resData => {this.Datas = resData;this.getLeaveData();console.log(this.Datas)});
  
  console.log(this.userid);

  } 
  getLeaveData() {
    this.LeaveDatas = JSON.parse(this.Datas._body).leaves;
      console.log(this.LeaveDatas);

  }
  onSubmit(leavedatas) {
      var count=0;
         var length =leavedatas.length;
      for(var i=0;i<=length-1;i++){
                var el =  <HTMLInputElement>document.getElementById('check'+leavedatas[i].leaveid);
                // console.log('leaveid' + leavedatas[i].leaveid + '::' + el.checked);
                var leaveid = leavedatas[i].leaveid;
                var empid = leavedatas[i].empid;
           
                   if(el != null && el.checked){
                   var url =this.url_deleteLeave.replace('empId',this.userid).replace('leaveId',leaveid);
                    console.log("helloooooooooooo"+url);
                    this.http.delete(url)
                  .subscribe(resData => {this.d_Data = resData;console.log(this.d_Data)});
                }
             
                 if(el != null && this.Datas.status == 200 && el.checked ) {
                   this.value=true;this.valuec=false;
                    console.log(this.Datas.status);
                     var b1 =  <HTMLInputElement>document.getElementById('check'+leavedatas[i].leaveid);
                     b1.disabled =true;
                       count++;
                  }
                  else if(el != null && el.checked==false && count==0)
                  {
                    this.valuec=true;
                    // alert('kindly select at least one');break;
                  }
        }

  }

hideonce()
{
  this.value=false;
  location.reload();

}
hideoncee()
{
  this.valuec=false;
}
haha()
{
    this.valuec=false;
    this.value=false;
}
}



