import { Component, OnInit } from '@angular/core';
import { Http, Headers, RequestOptions, Response } from '@angular/http';
import '../../assets/js/disableback.js';

import{LeavedetailsComponent} from '../leavedetails/leavedetails.component';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';
import { Cookie } from 'ng2-cookies/ng2-cookies';
import { Router } from '@angular/router';

@Component({
  templateUrl: './leaves.component.html',
  styleUrls: ['./leaves.component.css'],
})
export class LeavesComponent implements OnInit {

  public leaveType;
  public from;
  public to;
  public reason;
  public duration;
  public contact;
  public valgo: boolean = false;

  constructor(private http: Http, private router:Router) { }
  hidemego() {
    this.valgo = false;
    this.leaveType = null;
    this.from = null;
    this.to = null;
    this.reason = null;
    this.duration = null;
    this.contact = null;
  }

  resetme() {
    this.hidemego();
  }

  userdata() {
    let data =
      {
        "leaveType": this.leaveType,
        "From": this.from,
        "To": this.to,
        "Reason": this.reason,
        "Duration": this.duration,
        "Contact": this.contact

      }
    return data;
  }
  public userid;

  ngOnInit() {
    var t = this.http.get('http://localhost:3000/leaveTypes').subscribe(res => this.getme(res));
   // console.log('getting cookies' + Cookie.get('cookieName'));
   this.userid=Cookie.get('cookieName');
  }

  public k = [];
  getme(data) {
    var g = JSON.parse(data._body);
    this.k = g;
    return this.k;
  }

  postleaves() {
    var k = JSON.stringify(this.userdata());
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });
    return this.http.post('http://localhost:3000/'+this.userid+'/leaves', k, options).subscribe(res => this.myfn(res));
  }
logmeout()
{
    this.router.navigate(['/']);
    Cookie.delete('cookieName');
}
  myfn(data) {
    console.log(data);
    if (data.status == 200) {
      console.log(data.status);
      this.valgo = true;
    };
  }

    }



