import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Http, Headers, RequestOptions, Response } from '@angular/http';
import { Cookie } from 'ng2-cookies/ng2-cookies';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';


@Component({
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  constructor(private router: Router, private http: Http) { }

  ngOnInit() {
     
  }

  public username;
  public password;
  public localsaveid;
  public vaal: boolean = false;
  userdata() {
    let data =
      {
        "username": this.username,
        "password": this.password
      }
      // console.log(data);
    return data;
  }

  hidemefn() {
    this.vaal = false;
  }

  getUserId()
  {
    
  }


  validate() {
    var k = JSON.stringify(this.userdata());
     console.log(k);
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });
    this.http.post('http://localhost:3000/login', k, options).subscribe(res => {this.myfnlogin(res)});
  }

 public c;
  public count = 3;
  myfnlogin(data) {
    if (data._body == 'fail') {
      this.vaal = true;
      this.username = null;
      this.password = null;
      this.count--;
      if (this.count == 0) {
        this.router.navigate(['/notfound']);
      }

    }
    else {
      console.log("Data body:"+data._body);
      this.c=data._body;
      Cookie.set('cookieName', this.c);
      if(Cookie.get('cookieName')==this.c)
      {
      this.router.navigate(['/home']);
      }
    }

  }

}



