
import { Injectable } from '@angular/core'
import { Http, Response } from '@angular/http'
import { Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Rx';
import 'rxjs/add/operator/map';
import { Cookie } from 'ng2-cookies/ng2-cookies';

@Injectable()
export class LeaveService {
    public userid=Cookie.get('cookieName');
    private url_Holiday: string = "http://localhost:3000/publicHolidays";
    private url_getLeave:string = "http://localhost:3000/"+this.userid+"/leaves";
    
    constructor(private http: Http) { }

    getHolidayDetail() {
        return this.http.get(this.url_Holiday);
    }
    getLeave() {
        console.log("username is:"+this.userid);
        return this.http.get(this.url_getLeave);
        
    }
} 
 



