import { Component, OnInit } from '@angular/core';
import { addservice } from './sendasset.service';
import { Http, Headers, RequestOptions, Response } from '@angular/http';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';
@Component({

  templateUrl: './addasset.component.html',
  styleUrls: ['./addasset.component.css'],

})
export class AddassetComponent implements OnInit {

  constructor(private http: Http) { }
  public category;
  public itemtype;
  public serialnumber;
  public assetnumber;
  public brand;
  public model;
  public assetdescription;
  public allocationdate;
  public cubicalnumber;
  public locationdetails;
  public itemSelect;
  ngOnInit() {
    var t = this.http.get('http://localhost:3000/getCategory').subscribe(res => this.getme(res));
  }

  public k = [];
  getme(data) {
    var g = JSON.parse(data._body);
    this.k = g;
    console.log(this.k);
    return this.k;
  }

  public isDone = false;
  checkis() {

    if (this.isDone == false) {
      this.isDone = true;
    }
    else {
      this.isDone = false;
    }
  }
  userdata() {
    let data =
      {
        "category": this.category,
        "item": this.itemtype,
        "model": this.model,
        "brand": this.brand,
        "serialNo": this.serialnumber,
        "description": this.assetdescription,
        "status": "approved",
        "primaryUser": this.isDone,
        'cubicalnumber': this.cubicalnumber
      }
    return data;
  }

  addSubmit() {
    console.log("selcet from the item" + this.itemSelect);
    for(var i=0;i<this.k.length;i++)
    {
      if(this.k[i].name==this.itemSelect)
      {
        console.log( this.k[i].categoryId);
        this.category=this.k[i].categoryId;
      }
    }

    console.log(this.isDone);
    var k = JSON.stringify(this.userdata());
    console.log(k);
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });
    return this.http.post('http://localhost:3000/100035/assets', k, options).toPromise()
      .then(res => console.log(res));
  }
}


