import { Component, OnInit } from '@angular/core';
import { LeaveService } from '../leaves/leaves.service';

@Component({
  selector: 'app-holidaycalendar',
  templateUrl: './holidaycalendar.component.html',
  styleUrls: ['./holidaycalendar.component.css'],
  providers: [LeaveService]
})

export class HolidaycalendarComponent implements OnInit {

  Datas: any = [];
  holidaydatas: any;
  dateDatas: any = [];
  dayDatas: any = [];

  constructor(private leaveService: LeaveService) { }

  ngOnInit() {
    this.leaveService.getHolidayDetail().subscribe(resData => { this.Datas = resData; this.getHolidayData(); this.getDate(); this.getDay(); });

  }

  getHolidayData() {
    this.holidaydatas = JSON.parse(this.Datas._body);
  }

  getDate() {
    var date;
    for (var i = 0; i < this.holidaydatas.length; i++) {
      date = this.holidaydatas[i].date;
      this.dateDatas.push(date.substr(0, date.indexOf(',')));
    }

  }

  getDay() {
    var day;
    for (var i = 0; i < this.holidaydatas.length; i++) {
      day = this.holidaydatas[i].date;
      this.dayDatas.push(day.substr(day.indexOf('(') + 1, day.indexOf(')') - day.indexOf('(') - 1));
    }
  }

}



