import { BrowserModule } from '@angular/platform-browser';
import { NgModule, Component } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { RouterModule, Routes } from '@angular/router';
import { AppComponent } from './app.component';
import { HomeComponent } from './home/home.component';
import { LoginComponent } from './login/login.component';
import { LeavesComponent } from './leaves/leaves.component';
import { MyassetComponent } from './myasset/myasset.component';
import { AddassetComponent } from './addasset/addasset.component';
import { UndeclaredAssetComponent } from './undeclared-asset/undeclared-asset.component';
import { DeclaredAssetComponent } from './declared-asset/declared-asset.component';
import { CancelleaveComponent } from './cancelleave/cancelleave.component';
import { AlertModule } from 'ngx-bootstrap';
import { HolidaycalendarComponent } from './holidaycalendar/holidaycalendar.component';
import { LeavedetailsComponent } from './leavedetails/leavedetails.component';
import { CompensatoryoffComponent } from './compensatoryoff/compensatoryoff.component';
import { NotfoundComponent } from './notfound/notfound.component';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
// import {MdButtonModule, MdCheckboxModule} from '@angular/material';



const appRoutes: Routes = [
  { path: '', component: LoginComponent },
  { path: 'home', component: HomeComponent },
  { path: 'myasset', component: MyassetComponent },
  { path: 'addasset', component: AddassetComponent },
  { path: 'leaves', component: LeavesComponent },
  { path: 'notfound', component: NotfoundComponent }
];


@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    MyassetComponent,
    LoginComponent,
    LeavesComponent,
    AddassetComponent,
    UndeclaredAssetComponent,
    DeclaredAssetComponent,
    CancelleaveComponent,
    HolidaycalendarComponent,
    LeavedetailsComponent,
    CompensatoryoffComponent,
    NotfoundComponent,
  ],

  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    BrowserAnimationsModule,
    RouterModule.forRoot(appRoutes),
    AlertModule.forRoot()
  ],

  providers: [],

  bootstrap: [AppComponent]
})
export class AppModule { }



